import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import { loadDocs } from '../utils/loadDocs';
import TemplateA from '../templates/TemplateA';
import Header from '../regions/Header';

const COMPONENTS = loadDocs();

class HomePage extends PureComponent {
  render() {
    const sidebarContent = [];
    const content = [];

    content.push(<Header key="header" />);
    // content.push(<component.default key={component.default.name} />);

    COMPONENTS.forEach(component => {
      const { name } = component.default;
      const splitName = name.split(/(?=[A-Z])/);

      sidebarContent.push(
        <li key={name} ref={this[splitName]}>
          <Link to={splitName.join('-').toLowerCase()}>{splitName.slice(0, -1).join(' ')}</Link>
        </li>,
      );
    });

    return <TemplateA content={content} sidebarContent={sidebarContent} />;
  }
}

export default HomePage;



