import React, { PureComponent } from 'react';
import { Link } from 'react-router-dom';
import Types from 'prop-types';

import { loadDocs } from '../utils/loadDocs';
import TemplateA from '../templates/TemplateA';

const COMPONENTS = loadDocs();

class SinglePage extends PureComponent {
  render() {
    const sidebarContent = [];
    const { component } = this.props;

    COMPONENTS.forEach(comp => {
      const { name } = comp.default;
      const splitName = name.split(/(?=[A-Z])/);

      sidebarContent.push(
        <li key={name} ref={this[splitName]}>
          <Link to={splitName.join('-').toLowerCase()}>{splitName.slice(0, -1).join(' ')}</Link>
        </li>,
      );
    });
    return (
      <TemplateA
        content={[<component.default key={component.default.name} />]}
        sidebarContent={sidebarContent}
      />
    );
  }
}

SinglePage.propTypes = {
  component: Types.shape({}).isRequired,
};

export default SinglePage;



